<template>
    <div class="Acripts">
        <Carousel :type="'acripts'" :num="6"></Carousel>
        <div class="img_list">
            <div class="img_list_top">
                <span>视频资料</span>
            </div>
            <div class="img_box clearfloat">
                <div v-for="(car,index) in list" :key="index">
                  <img  :src="car.img">
                  <div class="text-bottom">{{car.name}}</div>
                </div>
            </div>
        </div>
		<div class="ul-box">
			<div class="img_list_top">
           		 <span>书籍资料</span>
        	</div>
			<div class="list clearfloat"  v-for="(car,index) in listBook" :key="index" @click="goPage(index)">
				<div class="left">
					<img :src="car.img">
				</div>
				<div class="right">
					<div class="title">{{car.title}}</div>
					<div class="text">{{car.text}}</div>
				</div>
			</div>
    	</div>
  </div>
</template>
<script>
import Carousel from '@/components/Carousel'
export default {
  name: 'Acripts',
  components: {
    Carousel
  },
  data () {
    return {
		list:[],
		listBook:[
		   {
			   img:require('../assets/activitya/1-c.png'),
			   title:"许麟庐艺术馆在烟台市博物馆开馆——市民可免费参观",
			   text:"2015年10月30日讯，许麟庐艺术馆开馆仪式于10月29日上午在市博物馆一楼中央大厅举行,"
					+"许麟庐师生友情展也在许麟庐艺术馆同步展出。今后,烟台市民可免费在许麟庐艺术馆欣赏到大师的珍贵作品。"
					+"下午,烟台市博物馆还举办了“写意人生——许麟庐书画艺术”学术研讨会,就许麟庐先生书画艺术研究成果进行了深入探讨和交流。"
		   },
		    {
			   img:require('../assets/activitya/1-c.png'),
			   title:"许麟庐艺术馆在烟台市博物馆开馆——市民可免费参观",
			   text:"2015年10月30日讯，许麟庐艺术馆开馆仪式于10月29日上午在市博物馆一楼中央大厅举行,"
					+"许麟庐师生友情展也在许麟庐艺术馆同步展出。今后,烟台市民可免费在许麟庐艺术馆欣赏到大师的珍贵作品。"
					+"下午,烟台市博物馆还举办了“写意人生——许麟庐书画艺术”学术研讨会,就许麟庐先生书画艺术研究成果进行了深入探讨和交流。"
		   },
		    {
			   img:require('../assets/activitya/1-c.png'),
			   title:"许麟庐艺术馆在烟台市博物馆开馆——市民可免费参观",
			   text:"2015年10月30日讯，许麟庐艺术馆开馆仪式于10月29日上午在市博物馆一楼中央大厅举行,"
					+"许麟庐师生友情展也在许麟庐艺术馆同步展出。今后,烟台市民可免费在许麟庐艺术馆欣赏到大师的珍贵作品。"
					+"下午,烟台市博物馆还举办了“写意人生——许麟庐书画艺术”学术研讨会,就许麟庐先生书画艺术研究成果进行了深入探讨和交流。"
		   }
	   ]
    }
  },
  methods:{
	  pushList(){
          var name = ['黄苗子、许麟庐、许化迟','许麟庐 黄苗子、许化迟王世襄',
          '黄苗子、王世襄、丁聪、黄永玉、许化迟等','刘海粟、许麟庐、许化迟',
          '许化迟与启功先生','许化迟与王世襄先生']
          for(var i = 0;i<6;i++){
              this.list.push(
                  {
                      img:require('../assets/home/'+(i+3)+'-y.jpg'),
                      name:name[i]
                  }
              )
          }
      }
  },
  mounted(){
   this.pushList();
  }
}
</script>
<style lang="less" scoped>
    .img_list{
      width: 1170px;
      margin: 0 auto;
  }
  .img_list_top{
      padding: 12px 0;
      text-align: center;
      font-size: 20px;
    color: #79242F;
       border-top: 2px solid  #79242F;
    border-bottom: 2px solid  #79242F;
    margin-bottom: 20px;
     position: relative;
    .know{
        position: absolute;
        right: 0;
        top:18px;
        font-size: 10px;
        color: #79242F;
    }
  }
  .img_box{
      div{
          width: 380px;
          float: left;
          margin-bottom: 10px;
          margin-right: 15px;
          img{
              width: 100%;
               height: 300px;
               display: block;
          }
      }
      div:nth-child(3n){
          margin-right: 0;
      }
      .text-bottom{
          height: 40px;
         background: #EFCAA9;
         line-height: 40px;
         text-align: center;
         font-size: 18px;
        color: #79242F;
      }
  }
  .ul-box{
	margin:  0 auto;
	width: 1170px;
	.left{
		width: 30%;
		height: 300px;
		float: left ;
		cursor: pointer;
		img{
			width: 100%;
			height: 100%;
		}
	}
	.right{
		width:70%;
		height: 300px;
		float: left ;
		padding: 10px;
		background: #EBC7A7;
		cursor: pointer;
		.title{
			font-size:18px;
			color: #79242F;
			margin-bottom: 10px;
		}
		.text{
			font-size:15px;
			color: #333333;
			line-height: 30px;
			margin-bottom: 30px;
		}
		span{
			font-size: 15px;
			color: #79242F;
		}

	}
}
  .list{
	  margin-bottom: 20px;
  }
</style>


